import { useState } from "react";
import {
  Box,
  Typography,
  Grid,
  Dialog,
  DialogTitle,
  DialogContent,
  Avatar,
  CardActionArea,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@mui/material";
import {
  Science,
  Bolt,
  Whatshot,
  ElectricalServices,
  Link as LinkIcon,
} from "@mui/icons-material";
import danialCell from "../assets/danial_cell.png";
import aluminiumCopper from "../assets/Aluminium-Copper.jpg";
import ironSilver from "../assets/Iron-Silver.png";
import pencilElectrolysis from "../assets/pencil.png";
import sodiumElectrolysis from "../assets/sodium.jpg";
import saltBridge from "../assets/salt.png";

//  Styling
const iconStyle = { color: "#1565c0", minWidth: "40px" };
const listStyle = {
  backgroundColor: "#f8f9fa",
  padding: "10px",
  borderRadius: "8px",
  marginBottom: "10px",
  textAlign: "right",
};

//  Data for Each Experiment (Right to Left)
const cellData = [
  {
    name: "  (Zn-Cu)",
    image: danialCell,
    content: [
      { icon: <Science sx={iconStyle} />, title: " ", details: " :  (Zn) - \n :  (Cu) - " },
      { icon: <ElectricalServices sx={iconStyle} />, title: "", details: "   (ZnSO)\n   (CuSO)\n      " },
      { icon: <Bolt sx={iconStyle} />, title: "  ", details: "   = 1.10V" },
      { icon: <Whatshot sx={iconStyle} />, title: "", details: "   .\n  .\n    ." },
    ],
  },
  {
    name: " - (Al-Cu)",
    image: aluminiumCopper,
    content: [
      { icon: <Science sx={iconStyle} />, title: " ", details: " :  (Al)\n :  (Cu)" },
      { icon: <ElectricalServices sx={iconStyle} />, title: "", details: "   (Al(NO))\n   (CuSO)" },
      { icon: <Bolt sx={iconStyle} />, title: "  ", details: "   = 2.00V" },
      { icon: <Whatshot sx={iconStyle} />, title: "", details: "   .\n   ." },
    ],
  },
  {
    name: " - (Fe-Ag)",
    image: ironSilver,
    content: [
      { icon: <Science sx={iconStyle} />, title: " ", details: " :  (Fe)\n :  (Ag)" },
      { icon: <ElectricalServices sx={iconStyle} />, title: "", details: "   (FeSO)\n   (AgNO)" },
      { icon: <Bolt sx={iconStyle} />, title: "  ", details: "   = 1.24V" },
      { icon: <Whatshot sx={iconStyle} />, title: "", details: "     .\n      ." },
    ],
  },
  {
    name: "    ( )",
    image: pencilElectrolysis,
    content: [
      { icon: <Science sx={iconStyle} />, title: " ", details: " :  (Pb) - \n :  (Pb) - " },
      { icon: <ElectricalServices sx={iconStyle} />, title: "", details: "  (HO)       NaSO  " },
      { icon: <Bolt sx={iconStyle} />, title: "  ", details: "   = 1.23V" },
      { icon: <Whatshot sx={iconStyle} />, title: "", details: "   .\n     .\n   ." },
    ],
  },
  {
    name: "     ( )",
    image: sodiumElectrolysis,
    content: [
      { icon: <Science sx={iconStyle} />, title: " ", details: " :   \n :     " },
      { icon: <ElectricalServices sx={iconStyle} />, title: "", details: "    (NaCl)   " },
      { icon: <Bolt sx={iconStyle} />, title: "  ", details: "   = 2.2V (  2.5-4.0V)" },
      { icon: <Whatshot sx={iconStyle} />, title: "", details: "   .\n    (NaOH)." },
    ],
  },
  {
    name: "  NaCl (  -  )",
    image: saltBridge,
    content: [
      { icon: <Science sx={iconStyle} />, title: " ", details: "  :   \n     ." },
      { icon: <LinkIcon sx={iconStyle} />, title: "  Na  Cl", details: " Na   .\n Cl   ." },
      { icon: <Whatshot sx={iconStyle} />, title: "", details: "     .\n     ." },
    ],
  },
];

const GalvanicTheory = () => {
  const [open, setOpen] = useState(false);
  const [selectedCell, setSelectedCell] = useState(null);

  const handleOpen = (cell) => {
    setSelectedCell(cell);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedCell(null);
  };

  return (<div style={{ marginTop: "20px" }}>
    <Box sx={{ textAlign: "center", padding: "20px", direction: "rtl", }}>
      <Typography color="white" variant="h4" sx={{ fontWeight: "bold" }}>
           -  
      </Typography>
      <Grid container spacing={4} justifyContent="center">
        {cellData.map((cell, index) => (
          <Grid item xs={12} sm={4} key={index}>
            <CardActionArea onClick={() => handleOpen(cell)}>
              <Avatar src={cell.image} sx={{ width: "120px", height: "120px", margin: "auto" }} />
              <Typography color="white" variant="h6">{cell.name}</Typography>
            </CardActionArea>
          </Grid>
        ))}
      </Grid>
      <Dialog open={open} onClose={handleClose} maxWidth="md" fullWidth dir="rtl">
        {selectedCell && (
          <>
            <DialogTitle sx={{ textAlign: "center", backgroundColor: "#1565c0", color: "white" }}>
              {selectedCell.name}
            </DialogTitle>
            <DialogContent>
              {selectedCell.content.map((section, index) => (
                <List key={index} sx={listStyle}>
                  <ListItemIcon>{section.icon}</ListItemIcon>
                  <ListItemText primary={section.details} sx={{ textAlign: "right" }} />
                </List>
              ))}
            </DialogContent>
          </>
        )}
      </Dialog>
    </Box></div>
  );
};

export default GalvanicTheory;
